import React from 'react'
import styled from 'styled-components'

import broker from '../../broker'
import ProgressBar from './ProgressBar'

const Box = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 48px;
  margin-bottom: 5px;
  /*
  border: 3px dashed ${p => p.loop ? 'blue' : 'transparent'};

  :hover {
    border: 3px dashed grey;
  }
  */
`

const QueueBox = styled.div`
  display: flex;
  height: 32px;
  border: 2px red;
  border-style: none none groove groove;
`

const QueueItem = styled.div`
  height: 30px;
  width: 30px;
  background-image: url(images/${p => p.icon});
  background-size: 100% 100%;
  ${p => p.curr ? 'border: solid purple 2px' : null}
`

export default class BuildQueue extends React.Component {
  state = {
    items: [],
    loop: true,
    progress: 0
  }
  
  constructor(props) {
    super()
    this.id = props.id
    broker.addListener(
      'update',
      { id: this.id, onmessage: this.onmessage }
    )
  }

  onmessage = body => this.setState(body.buildQueues[this.id])

  handleDoubleClick = () => broker.post({ sub: 'toggleloop', body: { id: this.id } })

  render() {
    return (
      <Box onDoubleClick={this.handleDoubleClick} loop={this.state.loop}>
        <QueueBox>
          {this.state.items.map((item, i) => (
            <QueueItem
              key={item.id+i}
              icon={item.icon}
              curr={this.state.currItem===i}
            />
          ))}
        </QueueBox>
        <ProgressBar progress={this.state.progress} />
      </Box>
    )
  }
}



